  WrapAuraThis = async (e, KOI_id) => {
    var _AURA = '';

    var _AURA_Allow = '';
    _AURA = await this.state.contract.methods.AURA(KOI_id).call();
    _AURA_Allow = await this.state.contract.methods.AURAallowance(KOI_id, 17).call();
    this.setState({ Player_WrapAura: true });

    if (_AURA > 0) {

      if (_AURA_Allow >= _AURA) {
        this.setState({ WrapAuraState: "Ready to Wrap" })
        await this.state.contract_AURA.methods.wrap(KOI_id, _AURA).send({ from: this.state.accounts[0] });
        this.setState({ WrapAuraState: "Wrap Aura Done" })
      } else {
        this.setState({ WrapAuraState: "Waiting for Confirmation" })
        await this.state.contract.methods.AURAapprove(KOI_id, 17, _AURA).send({ from: this.state.accounts[0] });
        this.setState({ WrapAuraState: "Ready to Wrap" })
        await this.state.contract_AURA.methods.wrap(KOI_id, _AURA).send({ from: this.state.accounts[0] });
        this.setState({ WrapAuraState: "Wrap Aura Done" })

      }
    }

  }
