  KOI_Profils = async (e, KOI_id) => {
    //event.preventDefault();
    // console.log(event);
    // var KOI_id = event.value;
    var _AURA = '';
    var _level = '';
    var _Luckymoney = '';
    var _log = '';
    var _Gender = '';
    var _TransferTimes = '';
    var _KOIDate = [];
    var _now = new Date().getTime() / 1000;
    var _LevelUpable = true;
    var _AURAClaimable = true;
    var _LuckyMoneyCollectable = true;
    var _WrapAura = true;
    var Luckymoney_required = '';
    var _AURA_Allow = '';

    _KOIDate = await this.state.contract.methods.KOI_Profile(KOI_id).call();
    _AURA_Allow = await this.state.contract.methods.AURAallowance(KOI_id, 17).call();

    if (_KOIDate) {
      _AURA = _KOIDate['_AURA'] / 1e18;
      _level = _KOIDate['_level'];
      _Luckymoney = _KOIDate['_Luckymoney'] / 1e18;
      _log = _KOIDate['_log'];
      Luckymoney_required = await this.state.contract.methods.Luckymoney_required(_level).call();
      if (_KOIDate['_Gender'] === '0') {
        _Gender = 'F'
      }
      else {
        _Gender = 'M'
      };
      _TransferTimes = _KOIDate['_TransferTimes'];
      _KOIDate = [];

      if (_now > _log) {
        _LuckyMoneyCollectable = false;
      }
      console.log(_level + "/" + _Luckymoney);
      if (_level > 1 && _Luckymoney > 7) {

        _AURAClaimable = false;
      }

      if (_Luckymoney > Luckymoney_required) {
        _LevelUpable = false;

      }

      if (_AURA > 0) {
        _WrapAura = false;
      }

      this.setState({ Player_KOI_id: KOI_id });
      this.setState({ Player_AURA: _AURA });
      this.setState({ Player_level: _level });
      this.setState({ Player_Luckymoney: _Luckymoney });
      this.setState({ Player_log: _log });
      this.setState({ Player_Gender: _Gender });
      this.setState({ Player_TransferTimes: _TransferTimes });
      this.setState({ Player_LuckyMoneyCollectable: _LuckyMoneyCollectable });
      this.setState({ Player_AURAClaimable: _AURAClaimable });
      this.setState({ Player_LevelUpable: _LevelUpable });
      this.setState({ Player_WrapAura: _WrapAura });

      if (_AURA_Allow >= _AURA && _AURA > 0) {
        this.setState({ WrapAuraState: "Ready to Wrap" })

      } else {
        this.setState({ WrapAuraState: "Wrap Aura" })

      }

    }
    this.handleShow();
  };


  handleMintAmountChange = (evt) => {
    if (rx_live.test(evt.target.value)) {
      this.setState({ MintAmount: evt.target.value });
    }
    if (evt.target.value > 20) {
      this.setState({ MintAmount: 20 });
    }
  };


  setShow = (_bool) => {
    this.setState({ show: _bool });
  }
  handleClose = () => { this.setShow(false) };
  handleShow = () => { this.setShow(true) };


  TranAURASelect = async (e) => {
    this.setState({ TranAURASelected: "You choose #" + e });
    this.setState({ TranAURASelected_key: e });
    this.setState({ NeedTranLength: "" });
    this.setState({ startCheckKoiAura: "" });


    var _YourRecord = [];
    var _NeedTran = [];
    var _KOI_AURA;
    var KOI_id;
    var _CheckedKoiAura = 0;

    _YourRecord = this.state.YourKois;
    for (let index = 0; index < _YourRecord.length; index++) {

      if (_YourRecord[index] !== e) {
        KOI_id = _YourRecord[index];

        if (_NeedTran.length < 120) {

          try {
            _KOI_AURA = await this.state.contract.methods.AURA(KOI_id).call();
            if (_KOI_AURA > 0) {
              _NeedTran.push(KOI_id);
              this.setState({ NeedTranLength: _NeedTran.length + "  KOI Ready" });
            }
            if (index % 10 === 0) {
              console.log(_NeedTran);
              await delay(5);
            }
          } catch (error) {

          }
        } else {
          window.alert("Max transfer 120 KOI once")
          index = _YourRecord.length;
        }
      }
      _CheckedKoiAura = index + 1;
      this.setState({ startCheckKoiAura: _CheckedKoiAura + " KOI checked " });

    }

    if (_NeedTran.length === 0) {
      window.alert("No KOI can transfer AURA");

    } else {
      this.setState({ ReadyTranAURAvisible: "visible" })
      this.setState({ NeedTran: _NeedTran })
    }

    console.log(_NeedTran);

  }

  StartTranAURA = async () => {
    await this.state.contract_KOITools.methods.AURAtransferSpecIDToOne(this.state.NeedTran, this.state.TranAURASelected_key).send({ from: this.state.accounts[0] });
    this.setState({ ReadyTranAURAvisible: "invisible" })

  }




  ThisPage = async (e) => {
    this.setState({ Page: e });
    var _ThisPageKois = [];
    var start = 0;
    var end = 0;

    if (this.state.YourKois.length > 24 * e) {
      start = (e - 1) * 24;
      end = (e * 24);

      _ThisPageKois = this.state.YourKois.slice(start, end);
      this.setState({ ThisPageKois: _ThisPageKois });
    } else {
      start = (e - 1) * 24;
      end = this.state.YourKois.length;

      _ThisPageKois = this.state.YourKois.slice(start, end);
      this.setState({ ThisPageKois: _ThisPageKois });
    }

  }

  ChangePage = async (e, j) => {

    var findLastPage;

    var totalPages = this.state.YourKois.length;

    var NowPage = this.state.Page;

    findLastPage = Math.ceil(totalPages / 24);



    if (j === "F") {
      this.ThisPage(1);
    }

    if (j === "-1" && NowPage > 1) {
      this.ThisPage(NowPage - 1);


    }

    if (j === "+1" && NowPage < findLastPage) {
      this.ThisPage(NowPage + 1);
    }

    if (j === "L" && NowPage < findLastPage) {
      this.ThisPage(findLastPage);

    }

  }

